
body{
  background: #757F9A;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, right top, left top, from(#D7DDE8), to(#757F9A));
  background: linear-gradient(to left, #D7DDE8, #757F9A); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.todo-list{
  text-align: center;
  -webkit-box-flex: 1;
      -ms-flex: 1 auto;
          flex: 1 auto;
}
.header{
  color: #fff;
}
.task-count{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 500px;
  margin: auto;
}
.task-count h2{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: left;
}
.content{
  width: 500px;
  margin: auto;
  background-color: #f2f2f2;
  border-radius: 5px;
}
.list{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 500px;
  margin: auto;
  padding: 20px 0;
  color: #808080;
  border-bottom: 1px solid #b3b3b3;
}
.list-item{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: left;
  font-size: 1.5em;
  font-weight: lighter;
}
.remove{
  -webkit-box-flex: 0.2;
      -ms-flex: 0.2;
          flex: 0.2;
  text-align: right;
  margin-right: 10px;
  font-size: 1.5em;
  cursor: pointer;
  font-weight: bolder;
}
input[type=checkbox]{
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  margin: auto 0 auto -50px;
}
.error{
  margin: 20px auto 5px;
  color: #d8112c;
  font-size: 1.5em;
}
.composer{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 500px;
  margin: 20px auto 5px;
}
input[type=text]{
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
  height: 40px;
  width: 40px;
  font-size: 1.5em;
  color: #808080;
  border-radius: 5px 0 0 5px;
  padding: 0 10px;
  background-color: #f2f2f2;
  border: none;
  border-right: 1px solid #808080;
}
input[type=time]{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 40px;
  width: 10px;
  font-size: 1.5em;
  color: #808080;
  background-color: #f2f2f2;
  border-radius: 0 5px 5px 0;
  border: none;
  cursor: pointer;
}
input[type=submit]{
  height: 40px;
  width: 500px;
  font-size: 1.5em;
  color: #808080;
  border-radius: 5px;
  background-color: #f2f2f2;
  border: none;
  cursor: pointer;
}
.done{
  text-decoration: line-through;
}
